export default class AppSliderController {
  constructor() {
    this.pageControll = 0;
    this.pageNumber = 1;
    this.contentContainer = document.querySelector('.content-container');
    this.currentItem = document.querySelector('.current-item');
    this.toolTip = document.querySelector('.tool-tip');
    this.firstItem = document.querySelector('.first-item');
    this.prevItem = document.querySelector('.prev-item');
  }

  resetSliderData() {
    this.firstItem.classList.toggle('slider-item-hidden', true);
    this.prevItem.classList.toggle('slider-item-hidden', true);
    this.contentContainer.style.transform = `translateX(${this.pageControll})`;
    this.currentItem.innerHTML = this.pageNumber;
    this.toolTip.innerHTML = this.pageNumber;
  }

  addSliderHandler() {
    const nextItem = document.querySelector('.next-item');
    const hideToolTip = () => {
      this.toolTip.classList.toggle('show-tip', false);
    };

    const showFirstTip = () => {
      this.toolTip.classList.toggle('show-tip', true);
      this.toolTip.innerHTML = '1';
    };

    const showPrevTip = () => {
      this.toolTip.classList.toggle('show-tip', true);
      this.toolTip.innerHTML = this.pageNumber - 1;
    };

    const hidePrevTip = () => {
      this.toolTip.classList.toggle('show-tip', false);
      this.toolTip.innerHTML = this.pageNumber - 1;
    };

    const showNextTip = () => {
      this.toolTip.classList.toggle('show-tip', true);
      this.toolTip.innerHTML = this.pageNumber + 1;
    };

    this.firstItem.addEventListener('mousedown', showFirstTip);
    this.firstItem.addEventListener('touchstart', showFirstTip);
    this.firstItem.addEventListener('mouseup', hideToolTip);
    this.firstItem.addEventListener('touchend', hideToolTip);

    this.firstItem.addEventListener('click', () => {
      this.prevItem.classList.toggle('slider-item-hidden', true);
      this.firstItem.classList.toggle('slider-item-hidden', true);

      this.pageControll = 0;
      this.pageNumber = 1;
      this.contentContainer.style.transform = `translateX(${-this.pageControll}%)`;
      this.toolTip.innerHTML = `${this.pageNumber}`;
      this.currentItem.innerHTML = `${this.pageNumber}`;
    });

    this.prevItem.addEventListener('mousedown', showPrevTip);
    this.prevItem.addEventListener('touchstart', showPrevTip);
    this.prevItem.addEventListener('mouseup', hidePrevTip);
    this.prevItem.addEventListener('touchend', hidePrevTip);

    this.prevItem.addEventListener('click', () => {
      this.pageControll -= 100;
      this.pageNumber -= 1;
      this.toolTip.innerHTML = `${this.pageNumber}`;
      this.currentItem.innerHTML = `${this.pageNumber}`;
      this.contentContainer.style.transform = `translateX(${-this.pageControll}%)`;

      if (this.pageNumber === 1) {
        this.prevItem.classList.toggle('slider-item-hidden', true);
      }
      if (this.pageNumber === 2) {
        this.firstItem.classList.toggle('slider-item-hidden', true);
      }
    });

    nextItem.addEventListener('mousedown', showNextTip);
    nextItem.addEventListener('touchstart', showNextTip);
    nextItem.addEventListener('mouseup', hideToolTip);
    nextItem.addEventListener('touchend', hideToolTip);

    nextItem.addEventListener('click', () => {
      this.prevItem.classList.remove('slider-item-hidden');

      this.pageControll += 100;
      this.pageNumber += 1;
      this.contentContainer.style.transform = `translateX(${-this.pageControll}%)`;

      this.toolTip.innerHTML = this.pageNumber;
      this.currentItem.innerHTML = this.pageNumber;

      if (this.currentItem.innerHTML === '3') {
        this.firstItem.classList.remove('slider-item-hidden');
      }
    });
  }

  addScrollHandler() {
    const main = document.querySelector('.main');

    let isDown = false;
    let walkPercent;
    let startX;

    function unify(e) {
      return e.changedTouches ? e.changedTouches[0] : e;
    }

    const touchDown = (e) => {
      this.toolTip.classList.toggle('show-tip', true);
      isDown = true;
      main.classList.add('active');
      startX = unify(e).pageX;
      this.contentContainer.style.transform = `translateX(${-this.pageControll}%)`;
    };

    const touchSlide = (e) => {
      if (!isDown) return;

      e.preventDefault();
      const x = unify(e).pageX;
      const walk = x - startX;
      walkPercent = 100 / window.innerWidth * walk;

      this.contentContainer.style.transform = `translateX(${-(this.pageControll + ((-walk) / 20))}%)`;
      if (-walkPercent > 10) {
        this.toolTip.innerHTML = this.pageNumber + 1;
      }
      if (-walkPercent < 10 && -walkPercent > -10) {
        this.toolTip.innerHTML = this.pageNumber;
      }
      if (-walkPercent < -10 && this.pageControll !== 0) {
        this.toolTip.innerHTML = this.pageNumber - 1;
      }
    };

    const touchUp = () => {
      if (-walkPercent > 10) {
        this.pageControll += 100;
        this.pageNumber += 1;
      }
      if (-walkPercent < -10 && this.pageControll !== 0) {
        this.pageControll -= 100;
        this.pageNumber -= 1;
      }
      walkPercent = 0;

      this.contentContainer.style.transform = `translateX(${-this.pageControll}%)`;
      this.toolTip.innerHTML = this.pageNumber;
      this.currentItem.innerHTML = this.pageNumber;
      this.toolTip.classList.toggle('show-tip', false);
      isDown = false;
      main.classList.remove('active');

      if (this.pageNumber === 2) {
        this.prevItem.classList.remove('slider-item-hidden');
        this.firstItem.classList.toggle('slider-item-hidden', true);
      }
      if (this.pageNumber === 1) {
        this.prevItem.classList.toggle('slider-item-hidden', true);
      }

      if (this.pageNumber === 3) {
        this.firstItem.classList.remove('slider-item-hidden');
      }
    };

    main.addEventListener('touchstart', touchDown);
    main.addEventListener('mousedown', touchDown);

    main.addEventListener('mouseleave', () => {
      isDown = false;
      main.classList.remove('active');
    });

    main.addEventListener('touchend', touchUp);
    main.addEventListener('mouseup', touchUp);

    main.addEventListener('touchmove', touchSlide, false);
    main.addEventListener('mousemove', touchSlide);
  }
}
